* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@font-face {
  font-family: 'IBM Plex Sans Arabic';
  src: url('./fonts/IBMPlexSansArabic-Bold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
}

body {
  font-family: 'IBM Plex Sans Arabic', sans-serif;
  background-color: #000;
  color: #fff;
  line-height: 1.6;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  background-color: #111;
  position: relative;
}

.language-switcher {
  margin-left: -1vw;
}

.language-switcher button {
  background-color: transparent;
  color: #fff;
  border: 1px solid #fff;
  padding: 0.5rem 1rem;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s ease;
 
}

.language-switcher button:hover {
  background-color: #d4af37;
  color: #000;
}

.navbar-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  gap: 2rem;
  margin: 0 auto;
}

.navbar-menu li a {
  color: #fff;
  text-decoration: none;
  font-size: 1rem;
  transition: color 0.3s ease;
}

.navbar-menu li a:hover {
  color: #d4af37;
}

.right-image {
  width: 150px;
  height: auto;
  margin-left: auto;
}

.menu-toggle {
  display: none;
  flex-direction: column;
  cursor: pointer;
}

.menu-toggle .bar {
  width: 25px;
  height: 3px;
  background-color: #fff;
  margin: 4px 0;
}

@media (max-width: 768px) {
  .menu-toggle {
    display: flex;
    position: absolute;
    top: 20px;
    right: 20px;
  }

  .navbar-menu {
    display: none;
    flex-direction: column;
    position: absolute;
    top: 60px;
    right: 10px;
    background-color: #333;
    width: 200px;
    border-radius: 5px;
    padding: 10px;
  }

  .navbar-menu.active {
    display: flex;
  }

  .navbar-menu li {
    margin: 10px 0;
  }

  .navbar {
    flex-direction: column;
    align-items: center;
  }

  .right-image {
    margin-left: 0;
    width: 120px;
  }

  .language-switcher {
    margin-top: 20px;
  }
}

.hero {
  padding: 10rem 10rem;
  background-image: url('assets/h1.png');
  background-size: 83%;
  background-position: center;
  background-repeat: no-repeat;
  color: #fff;
  font-family: 'Arial', sans-serif;
}

.hero h1 {
  font-family: 'IBM Plex Sans Arabic', sans-serif;
  font-size: 5rem;
  font-weight: bold;
  text-align: center;
  margin: 0;
}

.hero h2 {
  font-size: 1.5rem;
}

.hero p {
  font-size: 1rem;
  max-width: 700px;
  margin: 0 auto;
  line-height: 1.8;
}

.containerhome {
  text-align: right;
}

.section-title {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 2rem;
  color: #d4af37;
}

.about {
  padding: 4rem 0;
}

.containerabout {
  text-align: right;
  max-width: 800px;
  margin: 0 auto;
}

.about h1 {
  font-family: 'IBM Plex Sans Arabic', sans-serif;
  font-weight: bold;
  margin-bottom: 1rem;
  font-size: 3rem;
  text-align: right;
}

.about p {
  font-size: 1rem;
  text-align: justify;
  line-height: 1.8;
  direction: rtl;
  margin: 0;
}
/* services*/

.services {
  padding: 4rem 0;
}

.containerservices {
  max-width: 1200px;
  margin: 0 auto;
}

.services h1 {
  font-family: 'IBM Plex Sans Arabic', sans-serif;
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 2rem;
  text-align: right;
  padding-right: 17.5%;
}

.services-grid {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1.5rem;
}

.service-card {
  background: #222;
  border: 1px solid #444;
  border-radius: 10px;
  padding: 1.5rem;
  text-align: center;
  width: 250px;
}

.service-card h3 {
  font-size: 1.4rem;
  font-weight: bold;
  padding: 2rem;
  border-radius: 10%;
  margin-bottom: 1rem;
}

.service-card p {
  font-size: 1rem;
  color: #fff;
  line-height: 1.6;
  margin-top: 1rem;
}

@media (max-width: 768px) {
  .services-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr); /* Create two columns */
    gap: 1.5rem;
  }

  .service-card {
    width: 100%; /* Ensure each service card takes full width of its grid cell */
  }
}



/* team*/
.team {
  padding: 4rem 0;
  text-align: right;
  padding-right: 5%;
}

.containerteam {
  max-width: 1000px;
  margin: 0 auto;
}

.team h1 {
  font-family: 'IBM Plex Sans Arabic', sans-serif;
  font-weight: bold;
  margin-bottom: 2rem;
  font-size: 3rem;
  text-align: right;
  padding-right: 5%;
}

.team-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1.5rem;
}

.team-card {
  background-color: #030303;
  border-radius: 0;
  text-align: center;
  width: 200px;
  transition: transform 0.3s;
}

.team-card:hover {
  transform: scale(1.05);
}

.team-card img {
  width: 100%;
  height: auto;
  margin-bottom: 1rem;
  border-radius: 0;
}

.team-card h3 {
  font-size: 1.4rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
  color: #fff;
}

.team-card p {
  font-size: 1rem;
  color: #aaa;
  line-height: 1.6;
}

@media (max-width: 768px) {
  .team-grid {
    flex-wrap: wrap;
    gap: 1rem;
  }

  .team-card {
    width: calc(50% - 1rem);
  }

  .team h1 {
    font-size: 2rem;
    padding-right: 20%;
  }
}
/* */

.contact {
  padding: 4rem 0;
  background-color: #000;
  color: #fff;
  text-align: right;
}

.containercontact {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 1.5rem;
}

.maintitle {
  font-family: 'IBM Plex Sans Arabic', sans-serif;
  font-size: 2.5rem;
  color: #fff;
  text-align: right;
  margin-bottom: 3rem;
  padding-right: 15%;
}

.contact-info {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 2rem;
}

.contact-card {
  background-color: #1a1a1a;
  border: 1px solid #333;
  border-radius: 10px;
  padding: 1.5rem;
  width: 33%;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.5);
  text-align: right;
  transition: transform 0.3s, box-shadow 0.3s;
}



.contact h1 {
  font-family: 'IBM Plex Sans Arabic', sans-serif;
  font-weight: bold;
  margin-bottom: 2rem;
  font-size: 3rem;
  text-align: right;
  padding-right: 5%;
}

.contact-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.7);
}

.contact-card .flag {
  width: 30px;
  height: auto;
  margin-left: 0.5rem;
}

.contact-card-header {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-bottom: 1rem;
}

.contact-card h3 {
  font-size: 1.5rem;
  color: #fff;
  margin: 0;
}

.contact-card p {
  font-size: 1rem;
  line-height: 1.6;
  color: #ccc;
  padding-right: 0.9rem;
}
/* */
@media (max-width: 768px) {
  .contact-info {
    flex-direction: column;
    gap: 1.5rem;
  }

  .contact-card {
    width: 100%;
    padding: 1rem;
    text-align: right; /* Ensure text and content remain aligned to the right */
  }

  .contact-card .flag {
    width: 25px; /* Keep logo size consistent */
    margin-left: 0.5rem; /* Add spacing between text and logo */
  }

  .contact-card-header {
    flex-direction: row; /* Set to row to have the text first then the logo */
    justify-content: flex-end; /* Align the content to the right, which places text before the logo */
    gap: 0.5rem; /* Add spacing between the text and logo */
  }

  .contact h1 {
      font-size: 2rem;
      padding-right: 20%;
    }
  /* Change the order of cards */
  .contact-card:nth-child(1) {
    order: 2; /* Saudi Arabia card displays first */
  }

  .contact-card:nth-child(2) {
    order: 1; /* Morocco card displays second */
  }

  /* Adjust the header text (h3) in the contact cards */
  .contact-card h3 {
    font-size: 1.5rem;
    text-align: right; /* Align country names to the right */
    margin-right: 0; /* No extra margin needed */
  }
}



/* */

.footer {
  padding: 2rem 0;
  background-color: #111;
  border-top: 1px solid #000000;
}

.containerfooter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1.5rem;
}

.left-image {
  width: 150px;
  height: auto;
}

.right-image {
  width: 150px;
  height: auto;
}

@media (max-width: 768px) {
  .containerfooter {
    flex-direction: row;  /* Maintain row layout on mobile */
    justify-content: space-between;  /* Keep space between items */
    text-align: left;  /* Align the content to the left */
  }

  .footer p {
    font-size: 0.8rem;
  }

  .footer img {
    margin-bottom: 1rem;  /* Add space between the images */
    width: 120px;  /* Optional: reduce size of images on mobile */
  }

  .left-image {
    order: 1;  /* Place social icons on the left */
    margin-right: 1rem;  /* Add space to the right of the social icons */
  }

  .right-image {
    order: 2;  /* Place logo on the right */
  }
}